:root {
  --multiplyer: 1;
  --width: 43px;
  --height: 25px;
  --nodeTopMargin: 8px;
}

.node {
  width: calc(var(--multiplyer) * var(--width));
  height: calc(var(--multiplyer) * var(--height));
  display: inline-block;
  border: 1px solid white;

  background-color: grey;
  position: relative;
  z-index: 10;

  margin-top: var(--nodeTopMargin);
}

.node::after {
  content: "";
  background-color: inherit;
  width: calc(var(--multiplyer) * var(--width));
  height: calc(var(--multiplyer) * var(--height));

  position: absolute;
  top: 0px;
  left: 0px;
  transform: rotate(60deg);
  z-index: -1;
}

.node::before {
  content: "";
  background-color: inherit;
  width: calc(var(--multiplyer) * var(--width));
  height: calc(var(--multiplyer) * var(--height));

  position: absolute;
  top: 0px;
  left: 0px;
  transform: rotate(-60deg);
  z-index: -1;
}

.row-offset:first-child {
  margin-left: calc(var(--multiplyer) * var(--width));
}

.start {
  background-color: green;
}

.finish {
  background-color: rgb(189, 6, 6);
}

.wall {
  background-color: black;
}

.road {
  background-color: darkgreen;
}

.node-visited {
  background-color: royalblue;
}

.node-visited-road {
  background-color: rgb(45, 71, 151);
}

.node-shortest-path {
  background-color: sandybrown;
}

.node-shortest-path-road {
  background-color: rgb(172, 118, 72);
}

.node-focus {
  background-color: rgb(244, 205, 96);
}
.text {
  font-size: 5px;
}

@media only screen and (max-width: 600px) {
  :root {
    --multiplyer: 0.7;
  }

  .node {
    margin-top: 5px;
    border-top: 0;
    border-bottom: 0;
    border-left: 0;
  }

  .row-offset:first-child {
    margin-left: 30px;
  }
}
