@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

:root {
  --multiplyer: 1;
  --width: 43px;
  --height: 25px;
  --nodeTopMargin: 8px;
}

.node {
  width: calc(1 * 43px);
  width: calc(var(--multiplyer) * var(--width));
  height: calc(1 * 25px);
  height: calc(var(--multiplyer) * var(--height));
  display: inline-block;
  border: 1px solid white;

  background-color: grey;
  position: relative;
  z-index: 10;

  margin-top: 8px;

  margin-top: var(--nodeTopMargin);
}

.node::after {
  content: "";
  background-color: inherit;
  width: calc(1 * 43px);
  width: calc(var(--multiplyer) * var(--width));
  height: calc(1 * 25px);
  height: calc(var(--multiplyer) * var(--height));

  position: absolute;
  top: 0px;
  left: 0px;
  -webkit-transform: rotate(60deg);
          transform: rotate(60deg);
  z-index: -1;
}

.node::before {
  content: "";
  background-color: inherit;
  width: calc(1 * 43px);
  width: calc(var(--multiplyer) * var(--width));
  height: calc(1 * 25px);
  height: calc(var(--multiplyer) * var(--height));

  position: absolute;
  top: 0px;
  left: 0px;
  -webkit-transform: rotate(-60deg);
          transform: rotate(-60deg);
  z-index: -1;
}

.row-offset:first-child {
  margin-left: calc(1 * 43px);
  margin-left: calc(var(--multiplyer) * var(--width));
}

.start {
  background-color: green;
}

.finish {
  background-color: rgb(189, 6, 6);
}

.wall {
  background-color: black;
}

.road {
  background-color: darkgreen;
}

.node-visited {
  background-color: royalblue;
}

.node-visited-road {
  background-color: rgb(45, 71, 151);
}

.node-shortest-path {
  background-color: sandybrown;
}

.node-shortest-path-road {
  background-color: rgb(172, 118, 72);
}

.node-focus {
  background-color: rgb(244, 205, 96);
}
.text {
  font-size: 5px;
}

@media only screen and (max-width: 600px) {
  :root {
    --multiplyer: 0.7;
  }

  .node {
    margin-top: 5px;
    border-top: 0;
    border-bottom: 0;
    border-left: 0;
  }

  .row-offset:first-child {
    margin-left: 30px;
  }
}

:root {
  margin: 0;

  --startButtonColor: rgb(0, 128, 0);
  --finishButtonColor: rgb(189, 6, 6);

  --algoBtnTxtSize: 1.5rem;
  --light: #92de34;
  --dark: #69bc22;
  --text: #025600;
}

.grid {
  height: 70vh;
  width: 100%;
  margin: 50px 0 0;
}

#setStartBtn {
  background-color: rgba(0, 128, 0, 0.8);
}

#setFinishBtn {
  background-color: rgb(189, 6, 6);
  background-color: var(--finishButtonColor);
}

.menu {
  height: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.btn:hover {
  color: rgba(255, 255, 255, 1);
  box-shadow: 0 5px 15px rgba(134, 101, 158, 0.4);
  font-weight: bold;
}

.white-btn:hover {
  box-shadow: 0 5px 15px rgba(134, 101, 158, 0.4);
  font-weight: bold;
}

button {
  width: 100px;
  height: 40px;
  cursor: pointer;
  border-radius: 5px;
  border: none;
  flex-grow: 1;
}

.startButton {
  flex-grow: 1;
  display: block;
  cursor: pointer;
  outline: none;
  border: none;
  background-color: #92de34;
  background-color: var(--light);
  width: 200px;
  height: 40px;
  border-radius: 5px;
  font-size: 1.5rem;
  font-size: var(--algoBtnTxtSize);
  font-weight: 600;
  color: #025600;
  color: var(--text);
  background-size: 100% 100%;
  box-shadow: 0 0 0 7px #92de34 inset;
  box-shadow: 0 0 0 7px var(--light) inset;
}

.startButton:hover {
  background-image: linear-gradient(
    145deg,
    transparent 10%,
    #69bc22 10% 20%,
    transparent 20% 30%,
    #69bc22 30% 40%,
    transparent 40% 50%,
    #69bc22 50% 60%,
    transparent 60% 70%,
    #69bc22 70% 80%,
    transparent 80% 90%,
    #69bc22 90% 100%
  );
  background-image: linear-gradient(
    145deg,
    transparent 10%,
    var(--dark) 10% 20%,
    transparent 20% 30%,
    var(--dark) 30% 40%,
    transparent 40% 50%,
    var(--dark) 50% 60%,
    transparent 60% 70%,
    var(--dark) 70% 80%,
    transparent 80% 90%,
    var(--dark) 90% 100%
  );

  -webkit-animation: background 3s linear infinite;

          animation: background 3s linear infinite;
}

@-webkit-keyframes background {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 400px 0;
  }
}

@keyframes background {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 400px 0;
  }
}

.box {
  position: relative;
  flex-grow: 1;
}

.box select {
  background-color: rgb(5, 99, 175);
  color: white;
  padding-left: 10px;
  width: 100%;
  height: 40px;
  border: none;
  font-size: 20px;
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.2);
  -webkit-appearance: button;
  appearance: button;
  outline: none;
  border-radius: 5px;
}

select:hover {
  color: rgba(255, 255, 255, 0.6);
}

option {
  color: white;
}

.infobox {
  position: relative;
  width: 100%;
  height: 70px;
}

.info {
  position: absolute;
  display: flex;
  flex-direction: column;
  left: 50%;
  top: 10%;
  width: 300px;
  height: 70px;
  background-color: grey;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
}

.info-top {
  flex-basis: 30%;
}

.info-center {
  flex-basis: 70%;
  display: flex;
  flex-direction: row;
}

.info-side {
  width: 100%;
}
.infoFlex {
  display: flex;
  flex-direction: row;
}

.infoFlex label {
  width: 100%;
}

@media only screen and (max-width: 805px) and (min-width: 600px) {
  .infobox {
    margin-top: 4vh;
  }
}

@media only screen and (max-width: 600px) {
  :root {
    --algoBtnTxtSize: 1rem;
  }

  .grid {
    margin-top: 20px;
  }

  .menu {
    max-width: 600px;
    display: flex;
  }

  .startButton {
    flex-basis: 100%;
  }

  .infobox {
    margin-top: 10vh;
  }
}

