@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

:root {
  margin: 0;

  --startButtonColor: rgb(0, 128, 0);
  --finishButtonColor: rgb(189, 6, 6);

  --algoBtnTxtSize: 1.5rem;
  --light: #92de34;
  --dark: #69bc22;
  --text: #025600;
}

.grid {
  height: 70vh;
  width: 100%;
  margin: 50px 0 0;
}

#setStartBtn {
  background-color: rgba(0, 128, 0, 0.8);
}

#setFinishBtn {
  background-color: var(--finishButtonColor);
}

.menu {
  height: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.btn:hover {
  color: rgba(255, 255, 255, 1);
  box-shadow: 0 5px 15px rgba(134, 101, 158, 0.4);
  font-weight: bold;
}

.white-btn:hover {
  box-shadow: 0 5px 15px rgba(134, 101, 158, 0.4);
  font-weight: bold;
}

button {
  width: 100px;
  height: 40px;
  cursor: pointer;
  border-radius: 5px;
  border: none;
  flex-grow: 1;
}

.startButton {
  flex-grow: 1;
  display: block;
  cursor: pointer;
  outline: none;
  border: none;
  background-color: var(--light);
  width: 200px;
  height: 40px;
  border-radius: 5px;
  font-size: var(--algoBtnTxtSize);
  font-weight: 600;
  color: var(--text);
  background-size: 100% 100%;
  box-shadow: 0 0 0 7px var(--light) inset;
}

.startButton:hover {
  background-image: linear-gradient(
    145deg,
    transparent 10%,
    var(--dark) 10% 20%,
    transparent 20% 30%,
    var(--dark) 30% 40%,
    transparent 40% 50%,
    var(--dark) 50% 60%,
    transparent 60% 70%,
    var(--dark) 70% 80%,
    transparent 80% 90%,
    var(--dark) 90% 100%
  );

  animation: background 3s linear infinite;
}

@keyframes background {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 400px 0;
  }
}

.box {
  position: relative;
  flex-grow: 1;
}

.box select {
  background-color: rgb(5, 99, 175);
  color: white;
  padding-left: 10px;
  width: 100%;
  height: 40px;
  border: none;
  font-size: 20px;
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.2);
  -webkit-appearance: button;
  appearance: button;
  outline: none;
  border-radius: 5px;
}

select:hover {
  color: rgba(255, 255, 255, 0.6);
}

option {
  color: white;
}

.infobox {
  position: relative;
  width: 100%;
  height: 70px;
}

.info {
  position: absolute;
  display: flex;
  flex-direction: column;
  left: 50%;
  top: 10%;
  width: 300px;
  height: 70px;
  background-color: grey;
  transform: translate(-50%);
}

.info-top {
  flex-basis: 30%;
}

.info-center {
  flex-basis: 70%;
  display: flex;
  flex-direction: row;
}

.info-side {
  width: 100%;
}
.infoFlex {
  display: flex;
  flex-direction: row;
}

.infoFlex label {
  width: 100%;
}

@media only screen and (max-width: 805px) and (min-width: 600px) {
  .infobox {
    margin-top: 4vh;
  }
}

@media only screen and (max-width: 600px) {
  :root {
    --algoBtnTxtSize: 1rem;
  }

  .grid {
    margin-top: 20px;
  }

  .menu {
    max-width: 600px;
    display: flex;
  }

  .startButton {
    flex-basis: 100%;
  }

  .infobox {
    margin-top: 10vh;
  }
}
